.create-event-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
}

.create-event-header {
  margin-bottom: 2rem;
}

.create-event-header h1 {
  font-size: 1.8rem;
  color: #2d3748;
  margin: 0;
}

.create-event-form {
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.form-error {
  background-color: #fed7d7;
  color: #c53030;
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 4px;
  font-size: 0.9rem;
}

.form-section {
  padding: 1.5rem;
  border-bottom: 1px solid #e2e8f0;
}

.form-section h2 {
  font-size: 1.2rem;
  color: #2d3748;
  margin: 0 0 1.5rem 0;
}

.form-group {
  margin-bottom: 1.2rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
  color: #4a5568;
  font-weight: 500;
}

.form-group input,
.form-group textarea,
.form-group select {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #e2e8f0;
  border-radius: 4px;
  font-size: 1rem;
  transition: border-color 0.3s;
}

.form-group input:focus,
.form-group textarea:focus,
.form-group select:focus {
  outline: none;
  border-color: #4299e1;
  box-shadow: 0 0 0 1px #4299e1;
}

.form-row {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.form-group.half {
  flex: 1;
  min-width: 200px;
}

.location-button {
  background-color: #e2e8f0;
  color: #4a5568;
  border: none;
  padding: 0.75rem 1rem;
  border-radius: 4px;
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-top: 0.5rem;
}

.location-button:hover {
  background-color: #cbd5e0;
}

.location-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  background-color: #e2e8f0;
}

.location-button:disabled:hover {
  background-color: #e2e8f0;
}

/* Container for location buttons */
.location-buttons-container {
  display: flex;
  gap: 0.5rem;
  margin-top: 0.5rem;
  flex-wrap: wrap;
}

.location-buttons-container .location-button {
  margin-top: 0;
  flex: 1;
  min-width: 200px;
}

@media (max-width: 768px) {
  .location-buttons-container {
    flex-direction: column;
  }
  
  .location-buttons-container .location-button {
    width: 100%;
    min-width: auto;
  }
}

/* Location help section */
.location-help {
  margin-top: 1rem;
  padding: 0.75rem;
  background-color: #f7fafc;
  border-radius: 4px;
  border-left: 3px solid #4299e1;
}

.location-help p {
  margin: 0;
  font-size: 0.875rem;
  color: #4a5568;
  line-height: 1.4;
}

/* Location help link styling */
.coordinates-link {
  color: #4299e1;
  text-decoration: none;
  font-weight: 500;
  margin-left: 5px;
}

.coordinates-link:hover {
  color: #3182ce;
  text-decoration: underline;
}

.coordinates-link:focus {
  outline: 2px solid #4299e1;
  outline-offset: 2px;
  border-radius: 2px;
}

.image-preview {
  margin-top: 1rem;
  border-radius: 4px;
  overflow: hidden;
  max-height: 200px;
}

.image-preview img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  padding: 1.5rem;
}

.cancel-button,
.submit-button {
  padding: 0.75rem 1.5rem;
  border-radius: 4px;
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.3s;
}

.cancel-button {
  background-color: #e2e8f0;
  color: #4a5568;
  border: none;
}

.cancel-button:hover:not(:disabled) {
  background-color: #cbd5e0;
}

.submit-button {
  background-color: #4299e1;
  color: white;
  border: none;
}

.submit-button:hover:not(:disabled) {
  background-color: #3182ce;
}

.cancel-button:disabled,
.submit-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.create-event-success {
  background-color: #c6f6d5;
  color: #276749;
  text-align: center;
  padding: 2rem;
  border-radius: 8px;
  margin-bottom: 2rem;
}

.form-group select.multi-select {
  height: auto;
  min-height: 120px;
}

.help-text {
  margin-top: 0.5rem;
  font-size: 0.8rem;
  color: #718096;
  font-style: italic;
}

/* Competences help text styling */
.competences-help {
  font-size: 12px;
  color: #718096;
  margin-top: 4px;
  margin-bottom: 0;
  font-style: italic;
}

/* Recurring Events Section */
.recurring-section-title {
  font-size: 1.1rem;
  color: #2d3748;
  margin: 1.5rem 0 1rem 0;
  font-weight: 600;
  border-bottom: 1px solid #e2e8f0;
  padding-bottom: 0.5rem;
}

.recurring-preview {
  margin-top: 1rem;
  padding: 1rem;
  background-color: #f7fafc;
  border: 1px solid #e2e8f0;
  border-radius: 6px;
}

.recurring-preview h4 {
  margin: 0 0 0.75rem 0;
  font-size: 0.95rem;
  color: #4a5568;
  font-weight: 600;
}

.preview-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.preview-item {
  padding: 0.5rem 0;
  border-bottom: 1px solid #e2e8f0;
  font-size: 0.9rem;
  color: #2d3748;
  font-family: 'Monaco', 'Menlo', monospace;
}

.preview-item:last-child {
  border-bottom: none;
}

.preview-more {
  padding: 0.5rem 0;
  font-style: italic;
  color: #718096;
  font-size: 0.85rem;
}

.checkbox-group {
  display: flex;
  flex-wrap: wrap;
  gap: 0.8rem;
  margin-top: 0.5rem;
}

.checkbox-item {
  display: flex;
  align-items: center;
  background-color: #f7fafc;
  padding: 0.5rem 0.8rem;
  border-radius: 4px;
  border: 1px solid #e2e8f0;
  transition: all 0.2s;
  cursor: pointer;
}

.checkbox-item:hover {
  background-color: #edf2f7;
  border-color: #cbd5e0;
}

.checkbox-item input[type="checkbox"] {
  width: auto;
  margin-right: 0.5rem;
  cursor: pointer;
}

.checkbox-item label {
  margin-bottom: 0;
  font-weight: normal;
  cursor: pointer;
}

.field-error {
  color: #e53e3e;
  font-size: 0.8rem;
  margin-top: 0.5rem;
}

@media (max-width: 768px) {
  .create-event-container {
    padding: 1rem;
  }
  
  .form-actions {
    flex-direction: column-reverse;
  }
  
  .cancel-button,
  .submit-button {
    width: 100%;
  }
}

.loading-container {
  text-align: center;
  padding: 3rem;
  font-size: 1.2rem;
  color: #4a5568;
} 

/* Volunteer toggle styles */
.volunteer-toggle-section {
  margin-bottom: 1rem;
  padding: 1rem;
  background-color: #f7fafc;
  border: 1px solid #e2e8f0;
  border-radius: 6px;
}

.toggle-label {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-weight: 500;
  color: #4a5568;
}

.toggle-label input[type="checkbox"] {
  margin-right: 0.75rem;
  width: 16px;
  height: 16px;
  cursor: pointer;
}

.toggle-text {
  user-select: none;
}

.volunteer-input {
  margin-top: 1rem;
  padding: 1rem;
  background-color: white;
  border: 1px solid #cbd5e0;
  border-radius: 4px;
}

.volunteer-input label {
  font-weight: 500;
  color: #2d3748;
}

.volunteer-input input {
  margin-top: 0.5rem;
} 